[mol_chart_demo] {
    flex: 1 1 auto;
	display: flex;
	flex-direction: column;
	height: 100%;
	background: white;
	text-align: left;
}

[mol_chart_demo_header] {
	background: #3A5C86;
	flex: 0 0 5%;
	margin: 0;
	padding: .5em;
	color: white;
	display: flex;
	align-items: center;
	justify-content: center;
}

[mol_chart_demo_bodier] {
	padding: 2vmin;
}

[mol_chart_demo_charter] {
	margin: 2vmin;
}

[mol_chart_demo_legender_header] > * {
	display: table-cell;
	padding: 1.5vmin;
	vertical-align: middle;
	opacity: .5;
	text-align: right;
}

[mol_chart_demo_legender_header_sample] {
	text-align: center;
}

[mol_chart_demo_legender_header_title] {
	text-align: left;
}
