[mol_grapher_liner] {
	stroke-width: 3;
	fill: none;
}

[mol_grapher_liner_roper]:nth-last-of-type(-n+1) {
	stroke-dasharray: 5 5;
}

[mol_grapher_liner_roper]:hover {
	stroke: darkblue;
}

[mol_grapher_liner_knoter]:hover {
	stroke: darkblue;
}

[mol_grapher_liner_sampler_icon] {
	width: 100%;
	height: 3pt;
}
