[mol_chart] {
	display: flex;
	flex-direction: column;
	flex: 1 1 50vmin;
	/*max-height: 90vh;*/
}

[mol_chart_titler] {
	display: flex;
	justify-content: center;
	text-align: center;
	font-weight: bold;
	padding: .5em;
}

[mol_chart_plotter] {
	flex: 1 1 auto;
	height: 25vmin;
}

[mol_chart_plotter_svg] {
	width: 50vmin;
}

[mol_chart_legender] {
	width: auto;
	outline: none;
	font-size: .9rem;
}

[mol_chart_legender_lister] {
}

[mol_chart_legender_info] {
	display: table-row;
}

[mol_chart_legender_info] > * {
	display: table-cell;
	padding: 1.5vmin;
	vertical-align: middle;
}

[mol_chart_legender_info_titler] {
	flex: 1 1 auto;
}
